<template>
  <div class="extend-component">
    <div class="ivu-mt ivu-mb ivu-text-center">
      <SvgLogo />
      <h2>Welcome to {{ $config.titleSuffix }} App Components</h2>
    </div>
    <br />

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">VueXgplayer  <Rate :modelValue="1" disabled allow-half /></h2>
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
          <Space class="ivu-mb-8 ivu-text-left">
            <Button type="primary" ghost size="small" icon="ios-play" @click="play"></Button>
            <Button type="primary" ghost size="small" icon="md-pause" @click="pause"></Button>
            <Button type="primary" ghost size="small" icon="md-refresh" @click="replay"></Button>
            <Button type="primary" ghost size="small" icon="md-repeat" @click="changeSrc"></Button>
          </Space>
          <VueXgplayer :config="config" @player="handlePlayer" />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Tour  <Rate :modelValue="3" disabled allow-half /></h2>
        <span></span>
      </template>
      <Divider></Divider>
      <Space :size="24">
        <Button type="dashed" @click="current1 = 0"></Button>
        <Button ref="uploadRef1">Upload</Button>
        <Button ref="saveRef1" type="primary">Save</Button>
        <Button ref="moreRef1">More</Button>
        <Tour v-model="current1" :steps="steps1" />
      </Space>
      <Divider></Divider>
      <Space :size="24">
        <Button type="dashed" @click="current2 = 0"></Button>
        <Button ref="uploadRef2">Upload</Button>
        <Button ref="saveRef2" type="primary">Save</Button>
        <Button ref="moreRef2">More</Button>
        <Tour v-model="current2" :steps="steps2" :mask="false" />
      </Space>
      <Divider></Divider>
      <Space :size="24">
        <Button type="dashed" @click="current3 = 0"></Button>
        <Button ref="uploadRef3">Upload</Button>
        <Button ref="saveRef3" type="primary">Save</Button>
        <Button ref="moreRef3">More</Button>
        <Tour v-model="current3" :steps="steps3">
          <template #text="{ step }">
            <div v-if="step.index === 0" class="ivu-mb">
              <img
                style="width: 100%; height: 184px; object-fit: cover"
                src="https://gw.alipayobjects.com/mdn/rms_08e378/afts/img/A*zx7LTI_ECSAAAAAAAAAAAABkARQnAQ"
              />
            </div>
            <div>{{ step.description }}</div>
          </template>
        </Tour>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Markdown <Rate :modelValue="2" disabled allow-half /></h2>
      </template>
      <div style="text-align: initial">
        <Divider>ByteMdEditor</Divider>
        <ByteMdEditor v-model="text" />
        <Divider>ByteMdViewer</Divider>
        <ByteMdViewer :modelValue="text" />
      </div>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">TinymceEditor <Rate :modelValue="2" disabled allow-half /></h2>
      </template>
      <TinymceEditor v-model="text" />
      <Divider></Divider>
      <Input :modelValue="text" type="textarea" readonly show-word-limit :rows="5" />
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">FormContainer  <Rate :modelValue="4" disabled allow-half /></h2>
        <span></span>
      </template>
      <Space :size="24">
        <RadioGroup v-model="formContainerType">
          <Radio border label="modal"></Radio>
          <Radio border label="drawer"></Radio>
        </RadioGroup>
        <Button @click="formVisible = true"></Button>
      </Space>
      <div class="ivu-text-left">
        <FormContainer title="" :type="formContainerType" :width="520" v-model="formVisible">
          <Form label-position="top">
            <FormItem prop="title" label="">
              <Input placeholder="" />
            </FormItem>
            <FormItem prop="content" label="">
              <Input placeholder="" />
            </FormItem>
            <FormItem prop="date" label="">
              <DatePicker style="width: 100%" type="datetimerange" placeholder="" />
            </FormItem>
            <FormItem prop="room" label="">
              <Select placeholder="">
                <Option value="1">1</Option>
                <Option value="2">2</Option>
                <Option value="3">3</Option>
                <Option value="4">4</Option>
                <Option value="5">5</Option>
              </Select>
            </FormItem>
            <FormItem label="" prop="users">
              <Select multiple placeholder="">
                <Option value=""></Option>
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </FormItem>
            <FormItem label="" prop="remark">
              <Input :rows="3" type="textarea" placeholder="" show-word-limit :maxlength="200" />
            </FormItem>
          </Form>
        </FormContainer>
      </div>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Space  <Rate :modelValue="5" disabled allow-half /></h2>
        <span></span>
      </template>
      <Space direction="vertical" size="large" type="flex">
        <RadioGroup v-model="size">
          <Radio label="small">Small</Radio>
          <Radio label="middle">Middle</Radio>
          <Radio label="large">Large</Radio>
        </RadioGroup>
        <Space :size="size">
          <Button type="primary">Primary</Button>
          <Button>Default</Button>
          <Button type="dashed">Dashed</Button>
          <Button type="text">Text</Button>
        </Space>
        <Slider v-model="customSize" />
        <Space :size="customSize">
          <Button type="primary">Primary</Button>
          <Button>Default</Button>
          <Button type="dashed">Dashed</Button>
          <Button type="text">Text</Button>
        </Space>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Empty  <Rate :modelValue="4" disabled allow-half /></h2>
        <span></span>
      </template>
      <Empty />
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Region  <Rate :modelValue="1" disabled allow-half /></h2>
        <span> View UI  Form </span>
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8">
          <Divider>Region </Divider>
          <Region clearable />
          <Divider>City </Divider>
          <p class="ivu-mb"></p>
          <City clearable />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">IconSelect  <Rate :modelValue="1" disabled allow-half /></h2>
        <span
          > iView  v-model  View UI  Form
            Form </span
        >
      </template>
      <Row type="flex" justify="center" align="middle" class="ivu-text-left">
        <Col :xs="24" :sm="12" :md="12" :lg="12" :xl="8">
          <IconSelect clearable />
        </Col>
      </Row>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">TabsNav  <Rate :modelValue="3" disabled allow-half /></h2>
      </template>

      <Space wrap :size="[16, 8]">
        <Button @click="$tabsNav.close()"></Button>
        <Button @click="$tabsNav.open('/user/profile')"></Button>
        <Button @click="$tabsNav.close('/user/profile')"></Button>
        <Poptip confirm @on-ok="confirm">
          <template #title>
            <Form inline>
              <FormItem label="">
                <Input v-model="newTab" placeholder="Tab" />
              </FormItem>
            </Form>
          </template>
          <Button>Tab</Button>
        </Poptip>
        <Poptip confirm @on-ok="confirm2">
          <template #title>
            <div>
              <p>KEY <code>path</code> </p>
              <p>Tab</p>
              <p>Tab</p>
              <Form inline>
                <FormItem label="KEY">
                  <Input v-model="tabKey" />
                </FormItem>
                <FormItem label="">
                  <Input v-model="tabName" placeholder=" Tab " />
                </FormItem>
              </Form>
            </div>
          </template>
          <Button> Tab </Button>
        </Poptip>
      </Space>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">VxeLoading  <Rate :modelValue="5" disabled allow-half /></h2>
      </template>
      <div v-height="100" style="position: relative">
        <vxe-loading />
      </div>
    </Card>

    <Card shadow>
      <template #title>
        <h2 class="ivu-mb-8">Ellipsis  <Rate :modelValue="5" disabled allow-half /></h2>
        <span></span>
      </template>
      <div style="max-width: 600px" class="ivu-text-left ivu-margin-center">
        <Divider></Divider>
        <Ellipsis :text="text" :length="100" />

        <Divider> Tooltip</Divider>
        <Ellipsis :text="text" :length="100" tooltip />

        <Divider></Divider>
        <Ellipsis :text="text" :height="100" />

        <Divider></Divider>
        <Ellipsis :text="text" :lines="2" />
      </div>
    </Card>
  </div>
</template>

<script>
  import { Rate, Slider } from 'view-ui-plus'
  import { City, Tour, Region, Ellipsis, SvgLogo, IconSelect, VxeLoading, VueXgplayer, ByteMdEditor, ByteMdViewer, TinymceEditor } from '@/components'

  export default {
    name: 'Components',
    components: {
      City,
      Tour,
      Rate,
      Region,
      Slider,
      SvgLogo,
      Ellipsis,
      IconSelect,
      VxeLoading,
      VueXgplayer,
      ByteMdEditor,
      ByteMdViewer,
      TinymceEditor
    },
    data() {
      return {
        text:
          '',
        newTab: '',
        tabKey: '/dashboard/workplace',
        tabName: '',
        size: 'small',
        customSize: 8,
        formVisible: false,
        formContainerType: 'modal',
        current1: null,
        current2: null,
        current3: null,
        steps1: [
          {
            target: () => this.$refs.uploadRef1?.$el,
            title: '',
            description: '.'
          },
          {
            target: () => this.$refs.saveRef1?.$el,
            title: '',
            description: '.'
          },
          {
            target: () => this.$refs.moreRef1?.$el,
            title: '',
            description: '.'
          }
        ],
        steps2: [
          {
            target: () => this.$refs.uploadRef2?.$el,
            title: '',
            description: '.',
            popoverProps: {
              placement: 'top-start'
            }
          },
          {
            target: () => this.$refs.saveRef2?.$el,
            title: '',
            description: '.',
            popoverProps: {
              placement: 'bottom'
            }
          },
          {
            target: () => this.$refs.moreRef2?.$el,
            title: '',
            description: '.',
            popoverProps: {
              placement: 'top-end'
            }
          }
        ],
        steps3: [
          {
            title: ' iView Admin Plus ',
            description: ', , .'
          },
          {
            target: () => this.$refs.uploadRef3?.$el,
            title: '',
            description: '.'
          },
          {
            target: () => this.$refs.saveRef3?.$el,
            title: '',
            description: '.',
            mask: false
          },
          {
            target: () => this.$refs.moreRef3?.$el,
            title: '',
            description: '.'
          }
        ],
        config: {
          id: 'antd',
          loop: true,
          url: 'https://cdn.cnbj1.fds.api.mi-img.com/aife/xiaoai-open-platform/xiaoai6/evolution/h5-evolution-video.mp4'
        },
        player: null,
        ready: false
      }
    },
    methods: {
      confirm() {
        if (!this.newTab) {
          this.$Message.warning(' Tab ')
        }
        const { path } = this.$route
        this.$tabsNav.rename(path, this.newTab)
      },
      confirm2() {
        this.$tabsNav.rename(this.tabKey, this.tabName)
      },
      handlePlayer(player) {
        this.player = player
        this.player.on('play', () => {
          this.ready = true
        })
      },
      play() {
        this.player.paused && this.player.play()
      },
      pause() {
        this.player.paused || this.player.pause()
      },
      replay() {
        this.player.replay()
      },
      changeSrc() {
        const srcArray = [
          this.config.url,
          'https://cdn.cnbj1.fds.api.mi-img.com/product-images/xiaomibuds4prova5b03//modal.mp4',
          'https://cdn.cnbj1.fds.api.mi-img.com/product-images/xiaomibuds4teqz85/video_buds_4.mp4'
        ].filter(item => item !== this.player.src)
        this.player.src = srcArray[Math.floor(Math.random() * srcArray.length)]
        this.player.paused && this.player.play()
      }
    }
  }
</script>

<style lang="less">
  .extend-component {
    .ivu-mb.ivu-text-center {
      svg {
        width: 48px;
        height: 48px;
      }

      h2 {
        text-shadow: 0 6px 2px rgba(0, 0, 0, 0.3);
      }
    }

    .ivu-card {
      margin-bottom: 16px;
      text-align: center;

      &-head-title {
        display: block;

        & > span {
          color: #515a6e;
          font-size: 14px;
        }
      }
    }
  }
</style>
